body {
    background-color: maroon;
    text-align: center;
    justify-content: center;
    color: white;
    font-size: 24px;
    font-family: fantasy;
    text-shadow: 2px 2px 15px white;
}

.nameInput {
    text-align: center;
    justify-content: center;
    background-color: maroon;
    color: white;
    border: 0;
    font-size: 24px;
    font-family: fantasy;
    border-style: none;
    font-weight: bold;
    outline: none;
}

.list_style {
    list-style-type: none;
    padding: 0;
    margin: 0;
    color: black;
}

.trait_list {
    color: white;
    transition-property: color;
    transition-timing-function: ease-in-out;
    transition-duration: 5s;
}